body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  height: 100vh;
  color: aliceblue;
}
#root > div{
  height: 100vh;
}
a {
  color: aliceblue;
}

.particles {
    background: #000;
}
.main {
  height: 100%;
  width: 100%;
  position: fixed;
  top: 0;
  justify-content: center;
  display: flex;
  align-items: center;
  overflow: scroll;
}

::-webkit-scrollbar {
  width: 4px;
  height: 4px;
}
::-webkit-scrollbar-thumb {
  border-radius: 2px;
  background: #1f93b8;
}
::-webkit-scrollbar-track {
  border-radius: 10px;
}

.header {
  left:40px;
  position: fixed;
  display: flex;
  top: 20px;
  font-size: 24px;
  opacity: 0.6;
  align-items: center;
}
.header img {
  margin: 0px 10px 2px 2px;
}
.footer {
  font-size: 0.9em;
  right:40px;
  position: fixed;
  bottom: 20px;
  font-weight: lighter;
  color: #888888;
}
.footer a {
  color: #979797;
}
button {
  cursor: pointer;
  outline: none;
  color: yellow;
  font-size: 30px;
  background: #6fe4e4;
  height: 100px;
  width: 100px;
  border-radius: 50px;
  border: none;
}

button.wait {
  cursor: wait;
  background: #a8d6d6;
}

button.disable {
  cursor: not-allowed;
  background: #bccfd6;
  color: #a9bfc6;
}

.next-btn {
  text-align: center;
}

.creat_new_activity {
    font-size: 24px;
}
#input_container textarea {
    background: rgba(3, 169, 244, 0.45);
    height: 380px;
    min-width: 500px;
    max-width: 100%;
    font-size: 20px;
    padding: 10px 50px;
    line-height: 1.2em;
    color: #e1fde4;
    font-weight: lighter;
    word-spacing: 20px;
    -moz-tab-size: 20px;
         tab-size: 20px;
    tab-stops: 10px;
}
#input_container .notice_msg {
    text-align: right;
    height: 30px;
    font-size: 12px;
    padding: 2px;
}

.err_msg {
    color: red;
}

.lottery-pool header {
    font-size: 30px;
}

.name-cloud-container {
    position: fixed;
    bottom:0;
    pointer-events: none;
    width: 100%;
    padding-bottom: 50px;
}

.lottery-drawing{
    width: 100%;
    text-align: center;
}

header.prize-title{
    margin: 20px;
    font-size: 30px;
}

div.rolling {
    height: 400px;
    font-size: 80px;
}

.rolling .selectedParticipant .name {
    color: yellow;
}

.rolling .selectedParticipant .phone {
    font-size: 60px;
}
.rolling .selectedParticipant .phone .mask {
    font-size: 0.7em;
}
.lottery-setting {
    min-width: 500px;
}
.lottery-setting header {
    margin-bottom: 40px;
    font-size: 30px;
}
.lottery-setting input {
    text-align: center;
    border: none;
    color: white;
    border-bottom: dashed 1px #ded4d494;
    background: rgba(255, 255, 255, 0);
    outline: none;
}

.lottery-setting section.lottery-edit{
    margin: 20px 0px;
    height: 300px;
}
.lottery-item-body {
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    padding: 20px 30px;
    background: #6fe5e436;
    border-radius: 5px;
    height: 160px;
    overflow-y: scroll;
    box-shadow: #ffff0261 2px 4px 1px;
    border: solid 1px #ffffff30;
    font-size: 20px;
}
.lottery-item-body input {
    padding-bottom: 10px;
    font-size: 16px;
}
.lottery-item, .lottery-items-header {
    align-content: center;
    padding: 10px 10px;
    display: flex;

}

.lottery-item {
    border-bottom: 1px dotted #6f7375;
}

.lottery-item .lottery-item-remove {
    visibility: hidden;
    margin-left: 20px;
}
.lottery-item:hover .lottery-item-remove {
    visibility: visible;
}
.lottery-item:hover {
    background: #072028;
}
.drag-handle{
    cursor: move;
}
.lottery-item-name {
    margin-left: 20px;
    display: inline-block;
    flex: 1 1;
}
.lottery-item-count {
    display: inline;
}
.lottery-items-header {
    padding: 5px 25px 5px 50px;
    color: #ffff02;
    font-size: 1.2em;
}
.lottery-item-add {
    margin-top: 20px;
    cursor: pointer;
}
.activity-setting {
    min-width: 500px;

}
.activity-setting header {
    font-size: 30px;
}
.activity-setting input {
    width: 100%;
    font-size: 25px;
}

.activity-setting section{
    height: 100px;
    margin: 20px 0px;
}

.result_part {
    overflow: scroll;
    font-size: 20px;
    text-align: center;
}
.result_part header{
    color: yellow;
    font-size: 28px;
    margin-top: 10px;
}
.result_part .result_item{
    display: inline-block;
    padding: 2px 10px;
}
.result_part .result_item .result_name {

}
.result_part .result_item .result_phone {
    font-size: 16px;
    color: #efefef;
}
